import {
  BlogArticle,
  BlogPostFile,
  BlogTypeUnion,
  PublishTarget,
} from '@youfoundation/transit-lib';
import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import { BlogContentVm } from '../../../hooks/blogs/useBlogs';
import useChannels from '../../../hooks/blogs/useChannels';
import AclEditor from '../../AclEditor/AclEditor';

const BlogTeaser = ({
  className,
  blog,
  linkRoot,
}: {
  className?: string;
  blog: BlogPostFile<BlogContentVm>;
  linkRoot: string;
}) => {
  return (
    <Link to={`${linkRoot}${blog.content.slug ?? '#'}`} className="contents">
      <div
        className={`flex flex-nowrap px-5 py-8 transition-colors duration-200 hover:bg-slate-50 hover:dark:bg-slate-800 ${
          className ?? ''
        }`}
      >
        <div className="flex flex-col md:flex-grow md:flex-row">
          <div className="flex flex-shrink-0 md:order-3 md:mb-0 md:mt-3 md:w-32 md:flex-col md:text-right lg:w-64">
            <span className="title-font font-semibold text-gray-700 dark:text-white">
              {(blog.content as BlogTypeUnion).type.toUpperCase()}
            </span>
            <span className="pl-2 text-gray-500 md:mt-1 md:pl-0 md:text-sm">
              {new Date(blog.content.dateUnixTime).toLocaleDateString()}
            </span>
          </div>
          <div className="md:order-2 md:flex-grow">
            <h2 className="title-font mb-2 flex flex-row text-2xl font-medium text-gray-900 dark:text-white">
              {blog.content.caption}
              <BlogPublishTags publishTargets={blog.publishTargets} />
            </h2>
            {'abstract' in blog.content ? (
              <p className="leading-relaxed">{(blog.content as BlogArticle).abstract}</p>
            ) : null}
          </div>
        </div>
      </div>
    </Link>
  );
};

const BlogPublishTags = ({ publishTargets }: { publishTargets?: PublishTarget[] }) => {
  const [{ data: channels }] = useChannels();
  if (!publishTargets?.length) {
    return;
  }

  return (
    <span className="my-auto ml-2 flex flex-row flex-wrap">
      {publishTargets.map((target) => {
        const publishedChannel = channels.find((channel) => channel.channelId === target.channelId);
        if (!publishedChannel) {
          return null;
        }
        return (
          <span
            key={publishedChannel.channelId}
            className="mr-2 bg-green-300 px-2 py-1 text-xs"
            title={`${t('Published to')}: ${publishedChannel.name}`}
          >
            {publishedChannel.name}
          </span>
        );
      })}
    </span>
  );
};

export default BlogTeaser;
