import { BlogPostFile, ChannelTemplate } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import { BlogContentVm } from '../../../hooks/blogs/useBlogs';
import { ChannelDefinitionVm } from '../../../hooks/blogs/useChannels';
import BlogTeaser from '../../Blog/BlogTeaser/BlogTeaser';
import ActionButton from '../../Buttons/ActionButton';
import ChannelEditor from '../ChannelEditor/ChannelEditor';

const ChannelOverview = ({
  channel,
  blogs,
}: {
  channel: ChannelDefinitionVm | undefined;
  blogs: BlogPostFile<BlogContentVm>[];
}) => {
  const linkRoot = `/owner/blog/${channel ? channel.slug : 'all'}/`;
  const [isEditActive, setIsEditActive] = useState(false);

  return (
    <>
      {channel ? (
        isEditActive ? (
          <ChannelEditor
            key={channel.channelId}
            channel={channel}
            onCancel={() => setIsEditActive(false)}
          />
        ) : (
          <section className="items-center bg-slate-50 p-3 dark:bg-slate-800 sm:flex sm:flex-row">
            <p className="sm:mr-2">{channel.description ? channel.description : channel.name}</p>

            <p className="ml-auto">
              {t('Template')}:{' '}
              {parseInt(channel.templateId + '') === ChannelTemplate.LargeCards
                ? t('LargeCards')
                : parseInt(channel.templateId + '') === ChannelTemplate.ClassicBlog
                ? t('ClassicBlog')
                : t('MasonryLayout')}
            </p>
            <ActionButton
              type="secondary"
              className="sm:ml-2"
              onClick={() => setIsEditActive(true)}
            >
              {t('Edit Channel')}
            </ActionButton>
          </section>
        )
      ) : null}

      {blogs.length ? (
        <div className="-mx-5 divide-y-2 divide-gray-100 dark:divide-gray-800">
          {blogs.map((blog) => {
            return <BlogTeaser key={blog.content.itemKey} blog={blog} linkRoot={linkRoot} />;
          })}
        </div>
      ) : (
        <div className="mt-4">{t('no-data-found')}</div>
      )}
    </>
  );
};

export default ChannelOverview;
