import { ChannelTemplate } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { convertTextToSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import useChannel from '../../../hooks/blogs/useChannel';
import { ChannelDefinitionVm } from '../../../hooks/blogs/useChannels';
import AclEditor from '../../AclEditor/AclEditor';
import ActionButton from '../../Buttons/ActionButton';
import Input from '../../Form/Input';
import Select from '../../Form/Select';
import Textarea from '../../Form/Textarea';
import Section from '../../Sections/Section';

const ChannelEditor = ({
  channel,
  onCancel,
}: {
  channel: ChannelDefinitionVm;
  onCancel: () => void;
}) => {
  const [channelData, setChannelData] = useState({ ...channel });
  const { mutate: saveChannel, status: saveStatus } = useChannel().save;
  const navigate = useNavigate();

  if (!channel) {
    return;
  }

  const handleChange = (e: { target: { value: unknown; name: string } }) => {
    const newChannelData = { ...channelData };
    newChannelData[e.target.name] = e.target.value;

    setChannelData(newChannelData);
  };

  const tmpKeys = Object.keys(ChannelTemplate).filter((f) => !isNaN(Number(f)));
  const tmpVals = Object.keys(ChannelTemplate).filter((f) => isNaN(Number(f)));

  return (
    <Section
      title={
        <>
          <AclEditor
            key={channelData.channelId}
            acl={channelData.acl}
            onChange={(acl) => {
              handleChange({ target: { name: 'acl', value: acl } });
            }}
          />{' '}
          {`${t('Edit')}: ${channel.name}`}
        </>
      }
      isOpaqueBg={true}
    >
      <form
        onSubmit={(e) => {
          e.preventDefault();
          saveChannel(channelData, {
            onSuccess: () => {
              navigate(`/owner/blog/${convertTextToSlug(channelData.name)}`);
            },
          });
        }}
      >
        <div className="mb-5">
          <label htmlFor="name">{t('Name')}</label>
          <Input id="name" name="name" defaultValue={channel.name} onChange={handleChange} />
        </div>
        <div className="mb-5">
          <label htmlFor="description">{t('Description')}</label>
          <Textarea
            id="description"
            name="description"
            defaultValue={channel.description}
            onChange={handleChange}
          />
        </div>
        <div className="mb-5">
          <label htmlFor="template">{t('Template')}</label>
          <Select
            id="template"
            name="templateId"
            defaultValue={channel.templateId}
            onChange={handleChange}
          >
            <option>{t('Make a selection')}</option>
            {tmpKeys.map((tmpKey, index) => {
              return (
                <option value={tmpKey} key={tmpKey}>
                  {t(tmpVals[index])}
                </option>
              );
            })}
          </Select>
        </div>
        <div className="flex flex-row">
          <ActionButton
            type="secondary"
            className="ml-auto"
            onClick={(e) => {
              e.preventDefault();
              onCancel();
            }}
          >
            {t('Cancel')}
          </ActionButton>
          <ActionButton type="primary" className="ml-2" state={saveStatus}>
            {t('Save')}
          </ActionButton>
        </div>
      </form>
    </Section>
  );
};

export default ChannelEditor;
