import { useQuery } from '@tanstack/react-query';
import { ApiType, BlogContent, BlogPostFile, Client } from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

type useBlogPostsProps = {
  channelId?: string;
  page: string | undefined;
};

export interface BlogContentVm extends BlogContent {
  itemKey?: string;
}

const useBlogs = ({ channelId, page }: useBlogPostsProps = { page: undefined }) => {
  const pageSize = 50;
  const { getSharedSecret } = useAuth();

  const fetchBlogData = async ({ channelId, page }: useBlogPostsProps = { page: undefined }) => {
    const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

    const foundBlogPosts: BlogPostFile<BlogContentVm>[] =
      await client.blogPostProvider.getMasterPosts(page, pageSize);

    let filteredBlogPosts = foundBlogPosts.filter((post) => {
      if (!channelId) {
        return true;
      }
      return post.publishTargets.some((channel) => channel.channelId === channelId);
    });

    // Process blog posts (Only until fixed in data upload)
    filteredBlogPosts = filteredBlogPosts.map((item) => {
      return {
        ...item,
        content: {
          ...item.content,
          itemKey: `${item.content.channelId}_${item.content.id}`,
          slug: item.content.caption ? convertTextToSlug(item.content.caption) : item.content.id,
        },
      };
    });

    return filteredBlogPosts;
  };

  return useQuery(['blogs', channelId, page], () => fetchBlogData({ channelId, page }), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
  });
};

export default useBlogs;
