import { AccessControlList, SecurityGroupType, TargetDrive } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import usePortal from '../../../hooks/portal/usePortal';
import ActionButton from '../../Buttons/ActionButton';
import Radio from '../../Form/Radio';

const AclDialog = ({
  title,
  confirmText,
  isOpen,
  acl,
  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;

  acl: AccessControlList;

  onConfirm: (acl: AccessControlList) => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const [newAcl, setNewAcl] = useState<AccessControlList>(acl);

  if (!isOpen) {
    return null;
  }

  const saveAcl = () => {
    onConfirm(newAcl);
  };

  const updateSecurityGroup = (e) => {
    setNewAcl({ ...acl, requiredSecurityGroup: SecurityGroupType[e.target.value] });
  };

  const dialog = (
    <div className="relative z-50" aria-labelledby="modal-title" role="dialog" aria-modal="true">
      <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"></div>
      <div className="fixed inset-0 z-10 overflow-y-auto">
        <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
          <div className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all dark:bg-black sm:my-8 sm:w-full sm:max-w-lg">
            <div className="bg-white px-4 pt-5 pb-4 dark:bg-black dark:text-white sm:p-6 sm:pb-4">
              <div className="sm:flex sm:items-start">
                <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                  <h3
                    className="mb-3 text-lg font-medium leading-6 text-gray-900 dark:text-slate-50"
                    id="modal-title"
                  >
                    {title}
                  </h3>

                  <div>
                    <Radio
                      id="anonymous"
                      value="Anonymous"
                      name="securityGroup"
                      checked={newAcl.requiredSecurityGroup === SecurityGroupType.Anonymous}
                      onChange={updateSecurityGroup}
                    />
                    <label htmlFor="anonymous" className="ml-2">
                      Anonymous
                    </label>
                  </div>
                  <div>
                    <Radio
                      id="authenticated"
                      value="Authenticated"
                      name="securityGroup"
                      checked={newAcl.requiredSecurityGroup === SecurityGroupType.Authenticated}
                      onChange={updateSecurityGroup}
                    />
                    <label htmlFor="authenticated" className="ml-2">
                      Authenticated
                    </label>
                  </div>
                  <div>
                    <Radio
                      id="connected"
                      value="Connected"
                      name="securityGroup"
                      checked={newAcl.requiredSecurityGroup === SecurityGroupType.Connected}
                      onChange={updateSecurityGroup}
                    />
                    <label htmlFor="connected" className="ml-2">
                      Connected
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div className="-mx-2 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
              <ActionButton className="mx-2" onClick={saveAcl}>
                {confirmText ?? 'Save'}
              </ActionButton>
              <ActionButton className="mx-2" type="secondary" onClick={onCancel}>
                {t('Cancel')}
              </ActionButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return createPortal(dialog, target);
};

export default AclDialog;
