import { ChannelDefinition, SecurityGroupType } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { convertTextToSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import useChannel from '../../../hooks/blogs/useChannel';
import ActionButton from '../../Buttons/ActionButton';
import Input from '../../Form/Input';
import Section from '../../Sections/Section';

const ChannelCreator = () => {
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const { mutate: saveChannel, status: saveStatus } = useChannel().save;
  const navigate = useNavigate();

  const createChannel: React.FormEventHandler<HTMLFormElement> = (e) => {
    e.preventDefault();

    const newChannelDef: ChannelDefinition = {
      channelId: '',
      name: name,
      description: description,
      templateId: undefined,
      acl: { requiredSecurityGroup: SecurityGroupType.Owner },
    };

    saveChannel(newChannelDef, {
      onSuccess: () => {
        navigate(`/owner/blog/${convertTextToSlug(newChannelDef.name)}`);
      },
    });

    return false;
  };

  return (
    <Section title="New: channel" isOpaqueBg={true}>
      <form onSubmit={createChannel}>
        <div className="mb-5">
          <label htmlFor="channelName">{t('Name')}</label>
          <Input
            id="name"
            name="channelName"
            onChange={(e) => {
              setName(e.target.value);
            }}
            required
          />
        </div>
        <div className="mb-5">
          <label htmlFor="name">{t('Description')}</label>
          <Input
            id="name"
            name="channelDescription"
            onChange={(e) => {
              setDescription(e.target.value);
            }}
          />
        </div>
        <div className="flex flex-row">
          <ActionButton className="ml-auto" state={saveStatus}>
            {t('Add channel')}
          </ActionButton>
        </div>
      </form>
    </Section>
  );
};

export default ChannelCreator;
