import { FC, ReactNode } from 'react';
import { NavLink } from 'react-router-dom';

interface SubmenuProps {
  className?: string;
  items: { title: ReactNode; key: string; path: string; className?: string }[];
}

const Submenu: FC<SubmenuProps> = ({ className, items }) => {
  return (
    <div className={`flex ${className}`}>
      {items.map((item) => {
        return (
          // Only NavLink Supports isActive styling https://reactrouter.com/docs/en/v6/components/nav-link
          <NavLink
            className={({ isActive }) =>
              `flex-grow cursor-pointer border-b-2 py-2 px-1 text-lg ${
                isActive
                  ? 'border-indigo-500 text-indigo-500 dark:text-indigo-400'
                  : 'border-gray-300 transition-colors duration-300 hover:border-indigo-400 dark:border-gray-800 hover:dark:border-indigo-600'
              } ${item.className ?? ''}`
            }
            to={item.path}
            key={item.key}
            end
          >
            {item.title}
          </NavLink>
        );
      })}
    </div>
  );
};

export default Submenu;
