import { ReactNode } from 'react';

const Section = ({
  title,
  className,
  children,
  isOpaqueBg = false,
}: {
  title?: ReactNode;
  className?: string;
  children: ReactNode;
  isOpaqueBg?: boolean;
}) => {
  return (
    <section
      className={`my-5 rounded-md ${
        isOpaqueBg
          ? 'rounded-lg border-[1px] border-gray-200 border-opacity-80 dark:border-gray-700'
          : 'bg-slate-50 dark:bg-slate-800'
      } px-5  dark:text-slate-300 ${className ?? ''}`}
    >
      {title ? (
        <div
          className={`relative border-b-[1px] border-gray-200 border-opacity-80 py-5 transition-all duration-300 dark:border-gray-700`}
        >
          <h3 className="text-2xl dark:text-white">{title}</h3>
        </div>
      ) : null}
      <div className="py-5 ">{children}</div>
    </section>
  );
};

export default Section;
